import { Injectable } from '@angular/core';

@Injectable()
export class VideosService {
    public videos: Array<Video> = [
        new Video('1', '10:25', 25, '18...', 1),
        new Video('2', '13:25', 105, '...', 2),
        new Video('3', '7:25', 50, '', 3),
        new Video('4', '6:25', 30, 'GIF', 4),
        new Video('5', '20:25', 200, 'hi', 5),
        new Video('6', '15:25', 165, '', 6),
        new Video('7', '16:25', 185, '', 7),
        new Video('8', '17:25', 196, '', 8)
    ];
    public comments: Array<Comment> = [
        new Comment(1, new Date(), 'IPhone7', ''),
        new Comment(2, new Date(), 'Sumsong', ''),
        new Comment(3, new Date(), '2000 oppoR11', ''),
        new Comment(4, new Date(), 'Mix', ''),
        new Comment(5, new Date(), '', ''),
        new Comment(6, new Date(), 'vivo', 'ofhiof')
    ];

  constructor() { }

  public getVideoById(id: number): Video {
      let video: Video;
      video = this.videos.filter((value) => value.id === id)[0];
      console.log(video);
      return video;
  }

  public getAdVedios() {
    return this.videos;
  }

  public getSpreadVideos() {
    return this.videos.slice(0, 6);
  }
  public getMusicVideos() {
      return this.videos;
  }
  public getLiveVideos() {
      return this.videos;
  }
  public getDanceVideos() {
      return this.videos;
  }

}
export class Video {
    public name: string;
    public time: string;
    public size: number;
    public des: string;
    public id: number;
    constructor(name: string,
                time: string,
                size: number,
                des: string,
                id: number) {
        this.name = name;
        this.time = time;
        this.size = size;
        this.des = des;
        this.id = id;
    }
}
export class Comment {
    public user_id: number;
    public time: Date;
    public equipment: string;
    public user_comment: string;
    constructor(user_id: number,
                time: Date,
                equipment: string,
                user_comment: string
    ) {
        this.user_comment = user_comment;
        this.time = time;
        this.equipment = equipment;
        this.user_id = user_id;
    }
}



